import Vue from 'vue'
import App from './App.vue'
import VueResource from 'vue-resource'
import VueRouter from 'vue-router'
import {routes} from './routes'

// styles importation
// bootstrap from node modules
import 'bootstrap/dist/css/bootstrap.css'
// personalized style file
import './assets/css/teste.css'

// JS
import './assets/js/teste.js'
//import 'bootstrap/dist/js/bootstrap.js'


Vue.use(VueResource)

Vue.http.options.root = process.env.API_URL ? process.env.API_URL : 'http://localhost:3000'; // caso esteja em ambiente de produo vai pegar a varivel de ambiente 'API_URL'

Vue.use(VueRouter)

const router = new VueRouter({ 
  mode: 'history', /* Para no aparecer o # na url */
  routes 
})


new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



